.login-container{
    margin-top: 10%;
    max-width: 40%;
    max-height: max-content;
}
.login-form-1{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 5%;
}
.login-form-1 h3{
    text-align: center;
    color: #333;
}
.login-form-2{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 5%;
    background: #0062cc;
}
.login-form-2 h3{
    text-align: center;
    color: #fff;
}
.login-container form{
    padding: 5%;
}
.btnSubmit
{
    width: 50%;
    border-radius: 1rem;
    padding: 1.5%;
    border: none;
    cursor: pointer;
}
.login-form-1 .btnSubmit{
    color: #fff;
    background-color: #0062cc;
}
.login-form-2 .btnSubmit{
    background-color: #fff;
    color: #0062cc;
}
.login-form-2 .ForgetPwd{
    color: #fff;
    font-weight: 600;
    text-decoration: none;
}
.login-form-1 .ForgetPwd{
    color: #0062cc;
    font-weight: 600;
    text-decoration: none;
}

.link1 {
    color: #0062cc;
    text-decoration: none;
}

.link2 {
    color: #fff;
    text-decoration: none;
    
}

.centrar {
    display: flex;
    justify-content: center;
}

.right {
    display: inline-block;
    text-align: right;
}

.alert {
    color: rgb(235, 81, 81);
    font-size: 13px;
    padding: 0px !important;
    margin-bottom: 0px !important;
}

.alert2 {
    color:#fff;
    font-size: 13px;
    padding: 0px !important;
    margin-bottom: 0px !important;
}